@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  color: #4a4a4a;
}

.centered {
  margin: 3rem auto;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.focused {
  font-size: 3rem;
  font-weight: bold;
  color: white;
}

.btn {
  text-decoration: none;
  background-color: teal;
  color: white;
  border-radius: 4px;
  padding: 0.75rem 1.5rem;
  border: 1px solid teal;
  cursor: pointer;
}

.btn:hover,
.btn:active {
  background-color: #11acac;
  border-color: #11acac;
}

.btn--flat {
  cursor: pointer;
  font: inherit;
  color: teal;
  border: none;
  background-color: none;
  text-decoration: none;
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
}

.btn--flat:hover,
.btn--flat:active {
  background-color: teal;
  color: white;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1rem;
}

.page-header {
  border-bottom: 1px solid #ccc;
  padding-bottom: 1rem;
}

.page-title {
  font-size: 1.5rem;
  font-weight: bold;
  padding: 0 18px;
}

.content {
  padding-top: 7vh;
}

.flex {
  display: flex;
  align-items: center;
}

.flex.space-between {
  justify-content: space-between;
}

.table-head {
  text-transform: uppercase;
  font-size: 14px;
}

.table-body {
  font-size: 14px;
}

.table-head__cell {
  cursor: pointer;
}

.is-expanded > .select {
  width: 100%;
}

.control-panel {
  position: fixed;
  left: 0;
  bottom: 0;
  background-color: #393a3d;
  width: 100%;
  padding: 10px;
}

.form-section {
  border: 1px solid #ccc;
  padding: 2rem;
  border-radius: 10px;
  margin-bottom: 2rem;
}

.form-section_title {
  font-size: 1.25rem;
  margin-bottom: 1rem;
}

.error-message {
  font-size: 1rem;
  color: red;
  margin-left: 3px
}

.contract__detail {
  display: flex;
  align-items: center;
  margin-bottom: .5em;
}

.contract__detail.is-align-content-flex-start {
  align-items: flex-start;
}

.contract__detail h5 {
  font-size: 1.25rem;
  font-weight: 500;
  color: #dd3333;
  margin-right: 1em
}

.contract__contact-info {
  display: flex;
  flex-direction: column;
}

